@import url(https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap);

.App {
  width: 100%;
  text-align: center;
  box-sizing: border-box;
  justify-content: center;
}

.App-header {
  display: inline-block;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.App-body {
  display: flex;
  min-width: 400px;
  max-width: 500px;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  /* position: fixed; */
  top: 0;
  bottom: 0;
  width: 100%;
  min-height: 720px;
  margin-left: auto;
  margin-right: auto;
  flex-wrap: nowrap;
}
.main-img {
  margin-top: 30px;
  margin-bottom: 1ex;
  max-width: 300px;
}

.main-test {
  display: inline-block;
  margin: 0;
  width: 10ex;
  height: 2.5ex;
  line-height: 2.5ex;
  border-radius: 2ex;
  box-sizing: border-box;
  border-style: solid;
  border-width: 1px;
  border-color: black;
}

.main-text {
  margin: -1ex;
  font-size: 60px;
}

.start-btn {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 38px;
  transition: all .5s ease-in-out;
  border-radius: 35px;
  box-shadow: 0 1px 7px rgba(0,0,0,0.25);
  border: 0;
  width: 70px;
  height: 70px;
  margin-top: 30px;
  color: white;
  background-color: black;
}

.start-btn:hover {
  color: black;
  background-color: white;
}

@media screen and (max-width: 1090px) {
  .movies {
    grid-template-columns: 1fr;
    width: 100%;
  }
}
.question__container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    width: 100%;
    max-width: 400px;
    font-weight: 300;
  }
  
  .question__container span:first-child {
    font-size: 20px;
  }
  
  .question__container span:last-child {
    display: block;
    margin-top: 10px;
  }
.result__container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    width: 100%;
    max-width: 400px;
    font-weight: 300;
  }
  
  .result__container span:first-child {
    font-size: 20px;
  }
  
  .result__container span:last-child {
    display: block;
    margin-top: 10px;
  }
* {
    box-sizing: border-box;
}
  
body {
    margin: 0;
    padding: 0;
    font-family: 'Nanum Pen Script', cursive
        /*, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif*/;
    background-color: #eff3f7;
    height: 100%;
    line-height: 1.8;
}
